.wrapper {
    display: grid;
    grid-template-areas: "leftBlock rightBlock rightBlock rightBlock";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    margin-bottom: 30px;
}

.wrapper span {
    display: block;
}

.rightBlock {
    grid-area: rightBlock;
    text-align: left;
}

.leftBlock {
    grid-area: leftBlock;
}

.leftBlock img {
    width: 60px;
}